<template>
  <div name="mycal" class="mycal my-content">
    <div class="top">
      <form class="form-inline" style="height:385px;">
        <div class="form-group col-xs-12 left edit-block">
          <label>Team</label>
          <input type="text" class="form-control" v-model="model.teamName" />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>Odd</label>
          <input type="number" class="form-control" v-model="model.odd" />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>Rate</label>
          <input type="number" class="form-control" v-model="model.rate" />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>Home Status</label>
          <input type="number" class="form-control" v-model="model.hRate" />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>Guest Status</label>
          <input type="number" class="form-control" v-model="model.gRate" />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>HS5</label>
          <input type="number" class="form-control" v-model="model.hs" />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>GS5</label>
          <input type="number" class="form-control" v-model="model.gs" />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>HN5</label>
          <input type="number" class="form-control" v-model="model.hn" />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>GN5</label>
          <input type="number" class="form-control" v-model="model.gn" />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>HS10</label>
          <input type="number" class="form-control" v-model="model.hs2" />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>GS10</label>
          <input type="number" class="form-control" v-model="model.gs2" />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>HN10</label>
          <input type="number" class="form-control" v-model="model.hn2" />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>GN10</label>
          <input type="number" class="form-control" v-model="model.gn2" @keyup.enter="calculate" />
        </div>
      </form>
      <div style="padding:15px 15px 0 15px;">
        <table class="table table-striped table-bordered">
          <thead>
            <tr>
              <th width="33%">State5</th>
              <th width="33%">State10</th>
              <th>A/D</th>
            </tr>
          </thead>
          <tbody>
            <tr class="success">
              <td>{{left1+ '/' + right1}}</td>
              <td>{{left2 + '/' + right2}}</td>
              <td>{{stateChange +' | ' + stateChange2}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div class="button-area">
      <div class="btn-group">
        <button class="btn btn-danger" v-on:click='calculate'>Calculate</button>
        <button class="btn btn-success" v-on:click='resetStore'>{{message}}</button>
      </div>
    </div>
  </div>
</template>
<script>
import Store from '../services/store';
import CalUtil from '../services/cal';
export default {
  name: 'mycal',
  data() {
    return {
      model: {
        hRate: '',
        gRate: '',
        hs: '',
        gs: '',
        hn: '',
        gn: '',
        hs2: '',
        gs2: '',
        hn2: '',
        gn2: '',
        teamName: '',
        odd: '',
        rate: ''
      },
      left1: '',
      left2: '',
      right1: '',
      right2: '',
      message: 'Winner!',
      modelList: Store.fetch().length > 0 ? Store.fetch() : [],
      teamName: ''
    };
  },
  methods: {
    calculate: function() {
      // 转为数字
      for (let item in this.model) {
        if (item !== 'teamName') {
          this.model[item] = parseFloat(this.model[item]);
        }
      }
      // this.stateChange = `进攻:${this.model.hs - this.model.hs2},防守：${this.model
      //   .hn - this.model.hn2}`;
      // this.stateChange2 = `进攻:${this.model.gs - this.model.gs2},防守：${this.model
      //   .gn - this.model.gn2}`;
      this.left1 = parseFloat(
        (
          CalUtil.sortRate(this.model.hs, this.model.gn) * this.model.hRate
        ).toFixed(2)
      );
      this.left2 = parseFloat(
        (CalUtil.sortRate(this.model.hs2, this.model.gn2) * 1).toFixed(2)
      );
      this.right1 = parseFloat(
        (
          CalUtil.sortRate(this.model.gs, this.model.hn) * this.model.gRate
        ).toFixed(2)
      );
      this.right2 = parseFloat(
        (CalUtil.sortRate(this.model.gs2, this.model.hn2) * 1).toFixed(2)
      );
      this.message = (
        (this.left1 + this.left2 - (this.right1 + this.right2)) /
        2
      ).toFixed(2);
      let obj = {
        l1: this.left1,
        l2: this.left2,
        r1: this.right1,
        r2: this.right2,
        message: this.message,
        teamName: this.model.teamName,
        odd: this.model.odd,
        rate: this.model.rate
      };
      if (this.model.teamName) {
        this.addToList(obj);
      } else {
        alert('请输入Team!');
      }
      this.model = {
        hRate: '',
        gRate: '',
        hs: '',
        gs: '',
        hn: '',
        gn: '',
        hs2: '',
        gs2: '',
        hn2: '',
        gn2: '',
        teamName: '',
        odd: '',
        rate: ''
      };
    },
    addToList: function(obj) {
      this.modelList.unshift(obj);
      Store.save(this.modelList);
    },
    resetStore: function() {
      if (window.confirm('确定清空储存？')) {
        Store.reset();
      }
    }
  },
  computed: {
    stateChange: function() {
      return `${(
        parseFloat(this.model.hs) - parseFloat(this.model.hs2)
      ).toFixed(2)}/${(
        parseFloat(this.model.hn) - parseFloat(this.model.hn2)
      ).toFixed(2)}`;
    },
    stateChange2: function() {
      return `${(
        parseFloat(this.model.gs) - parseFloat(this.model.gs2)
      ).toFixed(2)}/${(
        parseFloat(this.model.gn) - parseFloat(this.model.gn2)
      ).toFixed(2)}`;
    }
  }
};
</script>
<style lang="scss" scoped>
.left:nth-child(2n) {
  border-right: 1px solid cornflowerblue;
}

.btn-default {
  width: 110px;
  color: #333;
  background-color: #fff;
  border-color: #ccc;
}

.record_list_model:nth-child(2n + 1) input {
  border: 5px groove #289ed4;
}

.add_record_btn {
  border-top: 2px solid;
  padding-top: 5px;
}

.record_btn_style {
  width: 49%;
  float: left;
  text-decoration: none;
  background: indianred;
  margin-top: 2px;
  padding: 0;
  height: 40px;
  line-height: 40px;
  font-size: 1.8rem;
}
.edit-block {
  display: flex !important;
  height: 55px;
  justify-content: center;
  align-content: center;
  flex-direction: column;
}
.form-group {
  margin-bottom: 0;
}
</style>



