class CalUtil {
    sortPercent(num) {
        num = Math.abs(num);
        if (num >= 0 && num <= 0.3) {
            return 0.5;
        } else if (num > 0.3 && num <= 0.6) {
            return 0.425;
        } else if (num > 0.6 && num < 1) {
            return 0.375;
        } else {
            return 0.25;
        }
    }
    // main方法
    sortRate(host, guest) {
        if (host > guest) {
            return guest + (host - guest) * this.sortPercent(host - guest);
        } else {
            return host + (guest - host) * this.sortPercent(guest - host);
        }
    }

}

export default new CalUtil();



